/* Section/Article */
section.special, article.special {
  text-align: center;
}

header.major {
  width: -moz-max-content;
  width: -webkit-max-content;
  width: -ms-max-content;
  width: max-content;
  margin-bottom: 2em;
}

header.major > :first-child {
  margin-bottom: 0;
  width: calc(100% + 0.5em);
}

header.major > :first-child:after {
  content: '';
  background-color: #FFFFFF;
  display: block;
  height: 2px;
  margin: 0.325em 0 0.5em 0;
  width: 100%;
}

header.major > p {
  font-size: 0.7em;
  font-weight: 600;
  letter-spacing: 0.25em;
  margin-bottom: 0;
  text-transform: uppercase;
}

body.is-ie header.major > :first-child:after {
  max-width: 9em;
}

body.is-ie header.major > h1:after {
  max-width: 100% !important;
}

@media screen and (max-width: 736px) {
  header.major > p br {
    display: none;
  }
}

/* Form */
form {
  margin: 0 0 2em 0;
}

form .field {
  margin: 0 0 2em 0;
}

form .field.half {
  width: 50%;
  float: left;
  padding: 0 0 0 1em;
}

form .field.half.first {
  padding: 0 1em 0 0;
}

form > .actions {
  margin: 2.5em 0 0 0 !important;
}

@media screen and (max-width: 736px) {
  form .field {
    margin: 0 0 1.5em 0;
  }
  form .field.half {
    padding: 0 0 0 0.75em;
  }
  form .field.half.first {
    padding: 0 0.75em 0 0;
  }
  form > .actions {
    margin: 2em 0 0 0 !important;
  }
}

@media screen and (max-width: 480px) {
  form .field.half {
    width: 100%;
    float: none;
    padding: 0;
  }
  form .field.half.first {
    padding: 0;
  }
}

label {
  color: #FFFFFF;
  display: block;
  font-size: 0.8em;
  font-weight: 600;
  letter-spacing: 0.25em;
  margin: 0 0 1em 0;
  text-transform: uppercase;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="search"],
input[type="url"],
select,
textarea {
  -moz-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: rgba(212, 212, 255, 0.035);
  border: none;
  border-radius: 0;
  color: inherit;
  display: block;
